@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    height: 100vh;
    font-family: "Play", "sans-serif";
}

.App {
    display: grid;
    grid-template-rows: 100px 80px 160px;
    color: #444;
}

h1 {
    -webkit-align-self: center;
            align-self: center;
    text-align: center;
}

button {
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
    font-family: "Play", "sans-serif";
    /* #29bf12 */
    background-color: #7ac74f;
    color: white;
    border: none;
    padding: 10px 16px;
    border-radius: 4px;
    width: 80px;
    font-size: 16px;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    cursor: pointer;
}

button[disabled] {
    opacity: 0.2;
    cursor: not-allowed;
}

.block-wrapper {
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
}

.block {
    grid-row: 3;
    width: 200px;
    border-radius: 20px;
    background: #7ac74f;
    color: white;
    text-align: center;
    padding: 30px 0;
    font-family: "arial";
    font-size: 24px;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
}

.error {
    grid-row: 3;
    align-self: center;
    justify-self: center;
    width: 200px;
    border-radius: 20px;
    /* #f21b3f */
    background: #e87461;
    color: white;
    text-align: center;
    padding: 30px 0;
    font-family: "arial";
    font-size: 24px;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
}

.result {
    grid-row: 3;
    align-self: center;
    justify-self: center;
    text-align: center;
}

.result p {
    margin-top: 10px;
    font-size: 18px;
    color: #7ac74f;
    font-weight: bold;
    text-align: center;
}

.result .score {
    font-size: 20px;
}

.result .rank {
    font-size: 24px;
}

.result .best {
    margin-top: 20px;
}

.result span {
    color: #444;
}

.dots {
    grid-row: 3;
    align-self: center;
    justify-self: center;
    font-size: 50px;
}

.dots span:nth-of-type(1) {
    -webkit-animation: dots-first 1s both, dots-second 1s 2s linear infinite;
            animation: dots-first 1s both, dots-second 1s 2s linear infinite;
}

.dots span:nth-of-type(2) {
    -webkit-animation: dots-first 1s 0.5s both, dots-second 1s 2s linear infinite;
            animation: dots-first 1s 0.5s both, dots-second 1s 2s linear infinite;
}

.dots span:nth-of-type(3) {
    -webkit-animation: dots-first 1s 1s both, dots-second 1s 2s linear infinite;
            animation: dots-first 1s 1s both, dots-second 1s 2s linear infinite;
}

@-webkit-keyframes dots-first {
    0% {
        opacity: 0;
    }

    33% {
        opacity: 0;
    }

    34% {
        opacity: 0;
    }

    66% {
        opacity: 0;
    }

    67% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes dots-first {
    0% {
        opacity: 0;
    }

    33% {
        opacity: 0;
    }

    34% {
        opacity: 0;
    }

    66% {
        opacity: 0;
    }

    67% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes dots-second {
    0% {
        opacity: 0;
    }

    49.99% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

@keyframes dots-second {
    0% {
        opacity: 0;
    }

    49.99% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

